#first {
    overflow: hidden;
    min-height: 100vh;
}

#intro {
    position: relative;
    overflow: visible;
    margin-top: 130px;
}

.intro__top {
    display: flex;
    justify-content: space-between;
    position: relative;
}

.intro__top-title {
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 35px;
}

.intro__top-title span {
    width: 100%;
    font-size: 80px;
    line-height: 100%;
}

.intro__top-left {
    display: flex;
    flex-direction: column;
}

.get-in-touch-wrap {
    display: flex;
    justify-content: flex-start;
}

.intro__get_in_touch,
.more_projects_link {
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 30px 45px;
    text-decoration: none;
    font-family: 'Cera Pro', sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 23px;
    letter-spacing: 0.01em;
    border: 1px solid var(--black);
    border-radius: 60px;
    transition: background .15s ease, padding .15s ease;
    appearance: none;
}

.intro__get_in_touch-text,
.more_projects_link-text {
    color: var(--black);
}

.intro__get_in_touch-text {
    transition: color .15s ease;
}

.intro__get_in_touch:hover,
.more_projects_link:hover {
    background-color: var(--black);
    padding: 30px 34px;
}

.intro__get_in_touch:hover .intro__get_in_touch-text,
.more_projects_link:hover .more_projects_link-text {
    color: #fff;
}

.intro__get_in_touch svg path,
.more_projects_link svg path {
    transition: stroke .15s ease;
}

.intro__get_in_touch:hover svg path,
.more_projects_link:hover svg path {
    stroke: #fff;
}

.intro__get_in_touch_arrow,
.more_projects_arrow,
.intro__top-right-arrow {
    display: flex;
    align-items: center;
}

.intro__arrow-start,
.more_projects_arrow-start,
.intro__top-right-arrow-start {
    width: 0;
    transition: width .15s ease;
}

.intro__top-right-more-link:hover .intro__top-right-arrow-start {
    width: 10px;
}

.intro__get_in_touch:hover .intro__arrow-start,
.more_projects_link:hover .more_projects_arrow-start {
    width: 22px;
}

.intro__top-right {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 104px;
}

.intro__top-right-info {
    font-family: 'Cera Pro';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    letter-spacing: 0.01em;
    max-width: 322px;
}

.intro__top-right-more-link {   
    display: flex;
    align-items: center;
    gap: 12px;
    margin-top: 30px;
    padding-bottom: 20px;
    border-bottom: 1px solid #040505;
    text-decoration: none;
    color: var(--black);
}

.intro__top-right-more-text {
    font-family: 'Cera Pro';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.03em;
}

.intro__parallax {
    position: absolute;
    bottom: 0;
    left: 0;
}

.cube-wrap {
    position: absolute;
    width: 408px;
    right: calc( ((100vw - 1226px) / 2) + 278px);
    bottom: 0;
}

.intro__parallax img {
    width: 550px;
}

#areas {
    padding-top: 200px;
}

.areas_title {
    margin-bottom: 60px;
}

.areas-grid-item {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-top: 40px;
    padding-bottom: 66px;
    border-top: 1px solid #E5E5E5;
}

.areas-grid-item:last-child {
    padding-bottom: 0;
}

.areas-heading {
    font-family: '小y Grotesk', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    letter-spacing: 0.01em;
    color: #040505;
    min-width: 291px;
}

.areas-heading sup {
    font-size: 13px;
    line-height: 13px;
    font-family: 'Cera Pro', sans-serif;
    top: -14px;
    margin-left: 12px;
    padding: 3px 6px;
    background-color: #f8f8f8;
    border-radius: 100px;
}

.areas-text {
    max-width: 300px;
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 160%;
    letter-spacing: 0.01em;
    color: #040505;
    min-width: 312px;
}

#selected {
    margin-top: 150px;
    background: linear-gradient(180deg, #FFFFFF 3.97%, #FFFFFF 3.98%, #FAFAFA 32.98%, #FFFFFF 66.49%, #FAFAFA 100%);
}

.selected_title {
    margin-bottom: 60px;
}

.selected-grid {
    padding-top: 65px;
    border-top: 1px solid var(--gray-tle-border);
}

.selected-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 65px;
}

.selected-item:first-child .selected-left {
    max-width: 420px;
}

.selected-left {
    width: 50%;
    max-width: 470px;
}

.selected-right {
    width: 50%;
    max-width: 600px;
}

.selected-icons {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    font-size: 0;
    margin-bottom: 35px;
}

.selected-icons img {
    width: 55px;
    height: 55px;
}

.selected-heading {
    font-size: 40px;
    line-height: 150%;
    margin-bottom: 24px;
    letter-spacing: 0.01em;
}

.selected-text {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 160%;
    letter-spacing: 0.01em;
    color: #040505;
    margin-bottom: 25px;
    max-width: 414px;
}

.selected-tags {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap: 10px;
}

.selected-tag {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.01em;
    color: #040505;
    text-decoration: none;
    color: var(--black);
    padding: 7px 10px;
    background-color: #F8F8F8;
    border-radius: 100px;
}

.more_projects {
    display: flex;
    justify-content: center;
}

.more_projects_link {
    padding: 30px 45px;
    width: auto;
    max-width: unset;
    gap: 12px;
}

/**
*
* Burger Menu
*
*
**/


.main-hdr .container {
    position: relative;
}

.menu_open {
    width: 100%;
    max-width: 620px;
    height: 100%;
    padding: 60px 70px 60px 70px;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1;
    background-color: #fff;
    display: none;
    transition: transform .3s ease;
    transform: translateX(100%);
}

.menu_open.moved:before {
    background: rgba(4, 5, 5, 0.03);
    content: '';
    top: 0;
    right: 100%;
    width: 200vw;
    height: 100vh;
    position: absolute;
}

.menu_open.opened {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.menu_open.moved {
    transform: translateX(0%);
}

.menu_open_grid {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: 100%;
}

.menu_main_list {
    margin-top: auto;
}


.menu_main_list ul {
    list-style-type: none;
}

.menu_main_item {
    margin-bottom: 26px;
}

.menu_main_item a {
    position: relative;
}

.menu_main_item a:before {
    content: '';
    position: absolute;
    bottom: -15px;
    left: 0;
    width: 0;
    height: 2px;
    background-color: var(--black);
    transition: width .3s ease;
}

.menu_main_item:hover a:before {
    width: 100%;
}

.menu_main_link {
    font-family: '小y Grotesk', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 43px;
    line-height: 60px;
    letter-spacing: 0.01em;
    color: #040505;
    text-decoration: none;
}

.menu_open_links {
    display: flex;
    flex-direction: column;
    gap: 23px;
    margin-top: 135px;
}

.menu_open_email-link {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 43px;
    line-height: 100%;
    letter-spacing: 0.01em;
    color: #040505;
    text-decoration: none;
}

.menu_open-socials {
    display: flex;
    align-items: center;
    gap: 25px;
}

.menu_open-social {
    font-size: 0;
    text-decoration: none;
    padding: 15px;
    position: relative;
}

.menu_open-social:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    /* background-color: #FAFAFA; */
    z-index: -1;
    border-radius: 100%;
    transition: background .4s ease;
}

/* .menu_open-social:hover:before {
    background-color: var(--black);
} */

.menu_open-social svg {
    width: 30px;
    height: 30px;
}
/* .menu_open-social svg path {
    transition: fill .4s ease;
}

.menu_open-social:hover svg path {
    fill: #fff;
} */

.menu_open-mini-lbl {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 100%;
    margin-bottom: 18px;
}

.email-mini-lbl {
    margin-bottom: 18px;
}

/**
*
* Recent publications
*
**/

#recent {
    padding: 140px 0;
    background-color: #FAFAFA;
}

.recent_title {
    padding-bottom: 60px;
    border-bottom: 1px solid var(--gray-tle-border);
}

.recent-swiper {
    margin-top: 60px;
    padding-bottom: 60px;
    border-bottom: 1px solid var(--gray-tle-border);
    z-index: 0;
}

.recent-swiper .swiper-slide:not(:first-child) {
    opacity: 0.3;
}

.recent-slide-img img {
    width: 100%;
}

.recent-date {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 160%;
    letter-spacing: 0.01em;
    color: #737373;
    margin-top: 35px;
    margin-bottom: 13px;
}

.recent-label {
    font-family: '小y Grotesk',sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.01em;
    color: var(--black);
}

.recent-tag {
    font-family: 'Cera Pro', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 160%;
    letter-spacing: 0.01em;
    color: #737373;
    margin-top: 13px;
}

@supports (-webkit-touch-callout: none) {
    /* CSS specific to iOS devices */
    #first {
        min-height: -webkit-fill-available;
    }
}